.page-footer {
  min-height: 120px;

  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;

  padding-left: 103px;
  padding-right: 103px;

  background-color: $color-tangaroa;

  @media (max-width: $vp-1365) {
    padding-left: 45px;
    padding-right: 45px;
  }

  @media (max-width: $vp-767) {
    min-height: 283px;

    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    padding-left: 15px;
    padding-right: 15px;
  }
}
